                        <Icon type="ios-keypad"></Icon>
                        成员审核
                    </MenuItem>     
                </router-link>         
            </div>
        </Menu>                        
    </Breadcrumb>
    <Card>        
        <router-view></router-view>               
    </Card>      
  </div>
</template>

<script>
export default {
    name: "membersctrl",
}
</script>
<style lang="scss" scoped>

</style>




